//header colors
$matte-black: rgb(26, 26, 26);

.header{
    align-items: center;
    background-color: whitesmoke;
    display: flex;
    flex-direction: row;
    height: 10vh;
    justify-content: space-between;
    padding-left: 25px;
    padding-right: 25px;
    position: relative;
}

.header__title{
    color: rgb(223, 91, 135);
    margin: 0;
    padding: 0;
}

.header__item{
    color: rgb(223, 91, 135);
    margin-right: 5px;
    padding: 6px 12px;
    transition: border .5s ease;
    -webkit-transition: border .5s ease;
    -moz-transition: border .5s ease;
    -ms-transition: border .5s ease;
    -o-transition: border .5s ease;
}

.header__item:hover{
    color: rgb(223, 91, 135);
    border: solid 1px rgb(223, 91, 135);
}

.is-active{
    color: rgb(223, 66, 118);
    font-weight: bold;
    border: solid 1px rgb(223, 91, 135);
    padding: 6px 12px;
}

.header__nav--btn{
    display: none;
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) and (orientation: Portrait) {
    .header{
        align-items: center;
        justify-content: space-between;
    }

    .header__nav{
        display: none;
    }

    .header__nav--btn{
        background: none !important;
        border: none !important;
        display: block;
        font-size: 3rem !important;
    }
}

